.nav__item-container {
    display: block;
    float: left;
}

.nav__item {
    box-sizing: border-box;
    display: block;
    padding: 0.8em 0.5em;
    color: white;

    &:hover {
        background-color: $lightGreen;
        color: white;
    }
}

.nav__active {
    background-color: $green;
}

@media screen and (max-width: 1280px) {

    .nav__item-container {
        font-size: 14px;
    }
}

@media screen and (max-width: 960px) {

    .nav__item-container {
        font-size: 16px;
        float: none;
    }

    .nav__item {
        padding: 1em;
    }
}
